<template>
  <div class="announcement-item" @click="goToDetail">
    <div class="announcement-item-title-container">
      <div class="title">
        <div v-if="!+itemData.have_read" class="dot" />
        <div class="hidden">{{ itemData.subject }}</div>
      </div>
      <p class="time">{{ handleTime(itemData.published_on) }}</p>
    </div>
  </div>
</template>

<script>
import AnnouncementService from '@services/announcement-service';
export default {
  props: {
    itemData: {
      type: Object,
      isRequired: true,
    },
  },
  methods: {
    handleTime(time) {
      const str = time.slice(5);
      const result = str.substring(0, str.length - 3);
      return result;
    },

    goToDetail() {
      const { id, have_read } = this.itemData;
      this.$router.push(`/announcement/detail/${id}`);
      if (!+have_read) {
        this.updateStatus();
      }
    },

    updateStatus() {
      const { id } = this.itemData;
      return AnnouncementService.updateAnnouncementStatus({
        announcement_ids: [id],
      }).then(res => {
        console.log(res);
      });
    },
  },
};
</script>
