<template>
  <app-container>
    <app-header title="公告" @click-right="updateStatus">
      <span slot="right">全部已读</span>
    </app-header>

    <div class="list">
      <van-pull-refresh
        v-model="isLoading"
        @refresh="onRefresh"
        class="refresh-wrapper"
      >
        <empty-page
          emptyMsg="暂无公告"
          :showEmpty="!announcementData.length"
          :loadingPage="this.loading"
        />
        <van-list
          v-model="loadingMore"
          :finished="finished"
          finished-text="— 干工程是有底线的 —"
          :immediate-check="false"
          @load="loadMore"
        >
          <div class="content">
            <div
              class="content-item"
              v-for="item in announcementData"
              :key="item.id"
            >
              <announcement-item :item-data="item" />
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </app-container>
</template>

<script>
import AnnouncementItem from '@views/announcement/item';
import AnnouncementService from '@services/announcement-service';
let pageIndex = 1;
export default {
  name: 'AnnouncementIndex',
  components: {
    AnnouncementItem,
  },
  data() {
    return {
      loading: false, // 是否正在加载数据，防止重复请求
      isLoading: false, // 是否处于加载状态，加载过程中不触发load事件
      loadingMore: false,
      finished: false,
      announcementData: [],
      recordCount: 0, // 列表总数
    };
  },
  activated() {
    this.init();
  },
  methods: {
    init() {
      this.loading = false;
      this.isLoading = false;
      this.loadingMore = false;
      this.finished = false;
      this.announcementData = [];
      pageIndex = 1;
      this.recordCount = 0;
      this.getList();
    },

    onRefresh() {
      if (this.loading) {
        return false;
      }
      this.isLoading = true;
      this.finished = false;
      this.getList()
        .then(() => {
          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
        });
    },

    loadMore() {
      this.finished = this.announcementData.length >= this.recordCount;

      if (this.finished) {
        this.loadingMore = false;
        return false;
      }

      return this.getList(false)
        .then(() => {
          this.loadingMore = false;
        })
        .catch(() => {
          this.loadingMore = false;
        });
    },

    getList(init = true) {
      if (this.loading) {
        return Promise.reject(false);
      }
      if (init) {
        pageIndex = 1;
      }
      this.loading = true;
      const params = {
        page_size: 20,
        page_index: pageIndex,
      };
      return AnnouncementService.getAnnouncementList(params)
        .then(res => {
          this.loading = false;
          const { announcementData, recordCount } = res;
          if (init) {
            this.announcementData = announcementData;
          } else {
            this.announcementData = this.announcementData.concat(
              announcementData
            );
          }
          pageIndex++;
          this.recordCount = recordCount;
          this.finished = this.announcementData.length >= recordCount;
        })
        .catch(err => {
          console.error(err);
          this.loading = false;
          return Promise.reject('获取失败');
        });
    },

    updateStatus() {
      if (!this.announcementData.length) {
        return;
      }
      const announcement_ids = [];
      this.announcementData.forEach(item => {
        if (!+item.have_read) {
          announcement_ids.push(item.id);
        }
      });
      return AnnouncementService.updateAnnouncementStatus({
        announcement_ids,
      }).then(() => {
        this.announcementData.forEach(item => {
          item.have_read = '1';
        });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.list {
  display: flex;
  position: relative;
  flex: 1;
  overflow: auto;

  .refresh-wrapper {
    display: flex;
    flex: 1;
    flex-direction: column;
    overflow: scroll;

    /deep/ .van-pull-refresh__track {
      flex: 1;
    }
  }
  .content {
    margin-top: 10px;
    background-color: #ffffff;
    &-item {
      padding: 15px;
      border-bottom: 1px solid #ececec;
    }
  }
}
</style>
